import React, {Component} from 'react';
import {MyTeaTable} from "../components/MyTeaTable";
import store from "../store";
import {getJwtToken} from "../lib/utils";
import axios from "axios";

/**
 * 
 * 
 */
class SysTea extends Component {

    constructor(props) {
        super(props);
        this.state={
            tea_data:[],
        }
        store.subscribe(()=>{
            let select_teacher_data = store.getState().user_tree_select.select_teacher;
            this.setState({tea_data:select_teacher_data});
        })
    }


    UNSAFE_componentWillMount() {

        const jwt = getJwtToken();
        axios.post('http://localhost:3000/api/back/findTea',{},{
            headers:{
                "Authorization":jwt
            }
        })
            .then(response=>{
                const {list} = response.data.data;
                //console.log(list);
                this.setState({
                    tea_data:list
                });
            })
    }


    render() {
        const {tea_data} = this.state;
        console.log(tea_data);

        return (
            <div>

                <MyTeaTable
                    columns = {columns}
                    data = {tea_data}
                    td={tea_data}
                />

            </div>
        );
    }


}

export default SysTea;

const columns = [
    {
        title: '',
        width: 100,
        dataIndex: 'teaName',
        key: 'name',
        fixed: 'left',
    },
    {
        title: '',
        width: 100,
        dataIndex: 'teaSex',
        key: 'sex',
        fixed: 'left',
    },
    { title: '',dataIndex: 'teaTel', key: '1' },
    { title: '',dataIndex: 'teaEmail', key: '2' },
    { title: '',dataIndex: 'teaPwd', key: '3' },
    { title: '',dataIndex: 'teaJob', key: '4' },
    { title: '',dataIndex: 'teaAddress', key: '5' },
    { title: '',dataIndex: 'teaDescription', key: '6' },
    {
        title: '',
        key: 'operation',
        fixed: 'right',
        width: 100,
        render: () => <a></a>,
    },
];

