import React, {Component} from 'react';
import {MyTeaTable} from "../components/MyTeaTable";
import {MyStuTable} from "../components/MyStuTable";
import store from "../store";
import {getJwtToken} from "../lib/utils";
import axios from "axios";

class SysStu extends Component {

    constructor(props) {
        super(props);
        this.state = {
            "stu_data":[],
        }
        store.subscribe(() => {
            this.setState({
                "stu_data": store.getState().user_tree_select.select_student,
            })
        })
    }

    UNSAFE_componentWillMount() {
        const jwt = getJwtToken();
        axios.post('http://localhost:3000/api/back/findStu',{},{
            headers:{
                "Authorization":jwt
            }
        })
            .then(response=>{
                const {list} = response.data.data;
                console.log(list);
                this.setState({
                    stu_data:list
                });
            })
    }

    render() {
        const {stu_data} = this.state;
        return (
            <div>

                <MyStuTable
                    columns = {stu_columns}
                    data = {stu_data}
                />

            </div>
        );
    }
}

export default SysStu;

const stu_columns = [
    {
        title: '',
        width: 100,
        dataIndex: 'userUsername',
        key: 'name',
        fixed: 'left',
    },
    {
        title: '',
        width: 100,
        dataIndex: 'userSex',
        key: 'sex',
        fixed: 'left',
    },
    { title: '',dataIndex: 'userPhone', key: '1' },
    { title: '',dataIndex: 'userEmail', key: '2' },
    { title: '',dataIndex: 'userMajor', key: '3' },
    { title: '',dataIndex: 'userAddress', key: '5' },
    { title: '',dataIndex: 'userDescription', key: '6' },
    {
        title: '',
        key: 'operation',
        fixed: 'right',
        width: 100,
        render: () => <a></a>,
    },
];

