import React, {Component} from 'react';
import {List} from "antd";
import axios from "axios";
import {getJwtToken} from "../lib/utils";

class SysInterface extends Component {

    componentDidMount() {
        const jwt = getJwtToken();
        axios.post('http://localhost:3000/api/back/interface',{},{
            headers:{
                "Authorization":jwt
            }
        })
            .then(response=>{console.log(response)})
            .catch(error=>console.log(error));
    }

    render() {
        return (
            <div>
                <List
                    size="large"
                    header={<div><h2></h2></div>}
                    split={true}
                    bordered
                    dataSource={dataList}
                    renderItem={item => <List.Item>{item}</List.Item>}
                />
                <List
                    size="large"
                    header={<div><h2></h2></div>}
                    split={true}
                    bordered
                    dataSource={LogList}
                    renderItem={item => <List.Item>{item}</List.Item>}
                />
            </div>
        );
    }
}

export default SysInterface;


const dataList = [
    'SUCCESS_CODE - 2000',
    'ERROR_CODE - 5555'
];

const LogList = [
    'LOGIN_MSG  -  ',
    'SEARCH_LOG  -  -',
    'SEARCH_USERINFO  -  -',
    'SEARCH_STUINFO  -  -',
    'SEARCH_TEAINFO  -  -',
    'SEARCH_DATABASE  -  -',
    'SEARCH_SWAGGER  -  -Swagger',
    'DELETE_LOG_SUCCESS  -  -',
    'DELETE_LOG_FAIL  -  -',
    "SEARCH_INTERFACE  -  -"
]